#topSlider {
  overflow: hidden;
  height: 400px; }
#topSlider .full-screen {
      background-size: 100%;
      -webkit-background-size: 100%;
      background-position: top center;
      background-repeat: no-repeat; }
.carousel-control {
  z-index: 10;
  width: 15% !important;
  opacity: 1; }
.carousel-control.left {
  left: -4%;
  top:  40%;
  background: url("images/topSliderarr.png") 130px 0 no-repeat; }
.carousel-control.right {
  right: -4%;
  top:  40%;
  background: url("images/topSliderarr.png") -67px 0 no-repeat; }
.topFoo {
  position: absolute;
  width: 100%;
  top: 0; }
.formbtn {
  background: $btm_col;
  padding: 8px 10px;
  width: 50%;
  margin: 0 auto;
  a {
    color: #ffff00;
    text-decoration: none; } }
.header-content {
  overflow: hidden;
  height: 100%;
  z-index: 2; }
.top-header {
  background-color:  rgba(0, 0, 0, 0.5);
  color: $invert_col;
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  .call-form {
    border: 0 solid red;
    margin-top: -8px;
    margin-bottom: 3px;
    img {
      float: left;
      width: 35px;
      height: 30px;
      margin-right: 10px; }
    a {
      color: #fff;
      font-size: 12px;
      text-decoration: underline;
      @include font('Open Sans');
      font-weight: 400;
      display: block;
      margin: 5px 0 0 15px; } }
  .top-tel {
    margin-left: 40px; }
  .top-kurs {
    color: $activ_col;
    @include font('Open Sans');
    font-weight: 400;
    font-size: 16px;
    margin: 10px 0 0;
    text-align: center; }
  .top-logo {
    a {
      display: block;
      text-align: center;
      img {
        margin: 0 auto; } } }
  .top-enter {
    @include font('Open Sans');
    font-weight: 400;
    font-size: 12px;
    margin: 10px 0 0;
    float: right;
    .top-searchIcon {
      float: left;
      width: 70%;
      img {
        width: 30px;
        float: right;
        cursor: pointer; } }
    a {
      display: block;
      float: right; } } }
.top-search {
  position: fixed;
  width: 100%;
  height: 100%;
  right: 0;
  top: 0;
  background-color: rgba(0,0,0,.8);
  z-index: 150;
  display: none;
  .close {
    width: 38px;
    height: 33px;
    cursor: pointer;
    opacity: .5;
    float: right;
    margin: 10px 0;
    background: url("images/srch-cross.png");
    &:hover {
      opacity: 1; } }

  .src-form {
    width: 90%;
    margin: 130px auto;
    border: 0 solid red;
    z-index: 1000;
    input[type="seacrh"],
    input[type="submit"] {
      border: none;
      width: 100%;
      background: none;
      padding: 6px 0;
      @include font("Open Sans"); }
    input[type="seacrh"] {
      color: #fff;
      font-size: 20px;
      border-bottom: 1px solid $activ_col; }
    input[type="seacrh"] {
      @include placeholder {
        color: #fff;
        font-size: 30px; } }
    input[type="submit"] {
      border: 1px solid $activ_col;
      font-size: 30px;
      border-radius: 3px;
      color: $activ_col; }
    .src-img {
      width: 100%;
      img {
        margin: 80px auto; } } } }
.header-logo {
  width: 90%;
  margin: 0 auto;
  text-align: center;
  line-height: 30px;
  img {
    margin: 0 auto; }
  h1 {
    font-size: 24px;
    text-transform: uppercase;
    font-weight: 700;
    color: $invert_col;
    text-shadow: 1px 0 1px rgba(0,0,0,.5);
    @include font('Open Sans'); } }
.choosevocation {
  background-color:  rgba(0, 0, 0, 0.5);
  margin: 0 auto;
  width: 95%;
  padding: 4px 0 0;
  .form-group {
    float: left;
    overflow: hidden;
    width: auto;
    margin-right: 16px;
    margin-bottom: 0; }
  label {
    display: block;
    margin: 0;
    padding: 5px 9px 0 0;
    color: $invert_col;
    text-transform: uppercase;
    font-weight: 400;
    font-size: 18px;
    position: relative;
    &.curort {
      text-align: right;
      background: url("images/vform-icons.png") no-repeat;
      background-position: 0 0; }
    &.hotel {
      text-align: right;
      background: url("images/vform-icons.png") no-repeat;
      background-position: 0 -35px; }
    &.checkin {
      text-align: right;
      background: url("images/vform-icons.png") no-repeat;
      background-position: -3px -60px;
      background-size: 35px; }
    &.departure {
      text-align: right;
      background: url("images/vform-icons.png") no-repeat;
      background-position: 0 -88px;
      background-size: 35px; }
    &.small {
      font-size: 13px;
      padding: 3px 0;
      text-align: center;
      @include font('Open Sans');
      margin-top: 6px; } }
  input[type="text"] {
    margin: 4px auto;
    width: 100%;
    border-radius: 6px; }
  input[type="image"] {
    margin: 20px 0 0 10px; } }

#passport {
  position: absolute;
  z-index: 2;
  background: url("images/passport.png") 0 0 no-repeat;
  -webkit-background-size: 100%;
  background-size: 100%;
  right: 0;
  bottom: -90px;
  width: 85px;
  height: 85px;
  a {
    text-indent: -9999px;
    display: block; }
  &:hover {
    background: url("images/passport.png") 0 -87px no-repeat;
    -webkit-background-size: 100%;
    background-size: 100%;
    a {
      text-indent: 0;
      font-size: 12px;
      color: #ffffff;
      text-decoration: none;
      text-align: center;
      padding: 18px;
      font-weight: bold;
      span {
        display: block; } } } }



