//colors
$activ_col: #fa7b0a;
$btm_col: #6585ff;
$footicon_col: #f99b53;
$blocks_bg: #000;
$invert_col: #fff;
$subfooter_col: #ededef;
$bmfooter_col: #494b5a;
$font_col: #292929;
$mainbg_col: #fff;
$contentbg_col: #e9e5e2;
$cardtabs_col: #e6e6e6;


* {
  margin: 0;
  padding: 0; }
ul {
  list-style: none;
  margin: 0;
  padding: 0; }
img {
  max-width: 100%;
  height: auto;
  display: block; }
textarea:focus,
input:focus {
  outline: none !important; }

html,
body {
  width: 100%;
  height: 100%; }
body {
  @include font('Roboto');
  font-size: 14px;
  font-weight: 400;
  background-color: $mainbg_col;
  max-width: 100%;
  min-width: 320px; }

section {
  display: block; }
.wrrap {
  width: 100%; }
#top,
#btn-connect {
  position: absolute;
  cursor: pointer;
  color: $invert_col;
  width: 70px;
  height: 70px;
  text-align: center;
  border-radius: 50%;
  background-color: $btm_col; }
#top {
  bottom: 7%;
  right: 10%;
  font-size: 45px; }
#btn-connect {
  top: 8%;
  left: 0;
  padding: 15px 0;
  @include font("Roboto"); }
